<template>
  <view class="service-assign">
    <navBar
      :navColor="'transparent'"
      title=""
      titleColor="#323233"
      backColor="#323233"
      :showBack="true"
    ></navBar>
    <view class="header">
      <!-- 顶部渐变背景+标题+背景图 -->
      <view class="order-header">
        <view class="header-content">
          <!-- <view class="header-title">健身服务
            <br />
            一屏览
          </view> -->
        </view>
      </view>
      <uv-tabs
        :list="tabs"
        customStyle="width: 100%;height: 80rpx;"
        itemStyle="width: 50%;height: 80rpx;box-sizing: border-box"
        @click="changeTabs"
        lineColor="#303133"
      ></uv-tabs>
      <view style="margin-top: 34rpx">
        <searchForm
          :statusList="statusList"
          :typeList="typeList"
          @search="hanldeSearch"
        ></searchForm>
      </view>
    </view>
    <!-- 滑动tab栏 -->
    <view class="order-list">
      <!-- :showApplyBtn="currentTab === 0" -->
      <service-order-item
        v-for="(item, index) in list"
        :key="index + 'orderItem'"
        :item="item"
        @reject="handleReject"
        @detail="handleDetail"
      />
      <empty v-if="showEmpty"></empty>
      <loadMore :status="loadStatus" v-if="showLoadMore" />
    </view>
  </view>
</template>

<script>
import ServiceOrderItem from './components/ServiceOrderItem.vue'
import listMixins from '@/mixins/listMixins.js'
import { getOrderList, orgDispatch, platformDispatch } from '@/apis/sportsService/serviceAssign.js'
import searchForm from './components/searchForm.vue'
import { useUserStore } from '@/store'
import { getServicesDictPage } from '@/apis/sportsService/common'

export default {
  components: {
    ServiceOrderItem,
    searchForm,
  },
  mixins: [listMixins],
  data() {
    return {
      tabs: [
        { name: '待处理', count: 8, key: 'platform_wait' },
        { name: '全部', count: 3, key: '' },
      ],
      currentTab: 0,
      orderList: [],
      page: 1,
      pageSize: 10,
      loadMoreStatus: 'loadmore',
      isRefreshing: false,
      // 模拟数据
      mockOrders: [
        {
          id: 1,
          categoryName: '科学健身指导',
          title: '南门社区广场舞指导',
          location: '南门社区广场',
          serviceTime: '2025/08/26 12:00:00',
          createTime: '2025/08/26 12:00',
          status: '待派单',
        },
        {
          id: 2,
          categoryName: '科学健身指导',
          title: '南门社区广场舞指导',
          location: '南门社区广场',
          serviceTime: '2025/08/26 12:00:00',
          createTime: '2025/08/26 12:00',
          status: '待服务',
        },
      ],
      typeList: [],
      statusList: [],
      formSearch: {
        dictId: '',
        serveWorkStatStatus: '',
        serveWorkDevStatus: 'platform_wait',
      },
    }
  },

  mounted() {
    this.getList()
    this.handelGetServicesDictPage()
    try {
      // 获取状态
      const data =
        JSON.parse(JSON.stringify(useUserStore().getEnumListByKey('serveWorkStatStatus'))) || []
      console.log({ data })
      data.unshift({ label: '全部状态', value: '' })
      this.statusList = data.map((item) => ({
        name: item.label,
        value: item.value,
      }))
    } catch (error) {
      console.error(error)
    }
  },

  methods: {
    // 获取订单列表
    async getList(refresh = false) {
      let param = {
        page: this.page,
        size: 10,
        ...this.formSearch,
      }

      // 模拟接口请求
      // await new Promise((resolve) => setTimeout(resolve, 500))
      const res = await getOrderList(param)
      this.getListExtend(res, refresh)
      // this.list = list
    },
    hanldeSearch(formSearch) {
      this.formSearch = Object.assign(this.formSearch, formSearch)
      this.resetData()
    },
    // 切换tab栏
    changeTabs(e) {
      console.log({ e })
      this.formSearch.serveWorkDevStatus = e.key
      this.resetData()
    },
    // 获取服务类型
    async handelGetServicesDictPage() {
      const res = await getServicesDictPage({ dict_type_sign: 'service_type' })
      if (res.code === 0) {
        this.typeList = res.data.listData || []
        this.typeList.unshift({ name: '全部', dictId: '' })
      }
    },
    // 点击列表项
    handleDetail(item) {
      uni.navigateTo({
        url: `/pagesSub/sportsService/serviceAssign/detail?id=${item.serveWorkId}&mode=plaform`,
      })
    },
    // 拒单
    async handleReject(item, fn) {
      console.log({ item })
      const res = await platformDispatch({
        processType: 'reject',
        serveWorkId: item.serveWorkId,
        rejectReason: item.rejectReason,
      })
      if (res.code === 0) {
        uni.showToast({
          title: '拒单成功',
          icon: 'success',
        })
        this.resetData()
        fn()
      }
    },
    // 完成服务
    async handleComplete(item) {
      uni.showModal({
        title: '提示',
        content: '确认完成服务吗？',
        success: (res) => {
          if (res.confirm) {
            uni.showToast({
              title: '操作成功',
              icon: 'success',
            })
            this.getOrderList(true)
          }
        },
      })
    },
  },
}
</script>

